.basket {
  &__circle {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 44px;
      height: 44px;
      border-radius: 50%;
      background-color: $blue;
  }
  &__image {
      font-size: 24px;
      line-height: 0;
      color: #fff;
  }
}